import React, { useState, useEffect } from "react";
import { Alert, Badge, Button, Form, ProgressBar } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  adddistrict,
  districtlist,
  districtdelete,
  deletewhatsuplink,
} from "../../reduxx/actions/actions";

function AddDistrict() {
  const dispatch = useDispatch();
  const [district, setDistrict] = useState("");
  const [taluk, setTaluk] = useState("");
  const handleSubmit = (e) => {
    e.preventDefault();
    e.currentTarget.reset();
    dispatch(adddistrict({ district: district, taluk: taluk }));
  };
  useEffect(() => {
    dispatch(districtlist());
  }, []);
  const districtlistt = useSelector(({ districtlist }) => districtlist.payload);
  return (
    <div>
      <div className="col-12 grid-margin stretch-card">
        <div className="card">
          <div className="card-body">
            <h4 className="card-title">Location</h4>
            <h3 className="card-description mt-5"> Add </h3>
            <form className="forms-sample" onSubmit={handleSubmit}>
              <Form.Group>
                <Form.Control
                  type="text"
                  className="form-control"
                  id="exampleInputName1"
                  name="district"
                  onChange={(e) => setDistrict(e.target.value)}
                  placeholder="Please Enter District"
                  required
                />
              </Form.Group>
              <Form.Group>
                <Form.Control
                  type="text"
                  className="form-control"
                  id="exampleInputName1"
                  name="taluk"
                  onChange={(e) => setTaluk(e.target.value)}
                  placeholder="Please Enter Taluk"
                  required
                />
              </Form.Group>

              <button type="submit" className="btn btn-gradient-primary mr-2">
                Submit
              </button>
            </form>
            <h3 className="card-description mt-5"> List </h3>
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th> S No </th>
                    <th> District </th>
                    <th> Taluk </th>
                    <th> Created </th>
                    <th> Actions </th>
                  </tr>
                </thead>
                <tbody>
                  {Array.isArray(districtlistt) ? (
                    districtlistt.reverse().map((location, i) => {
                      return (
                        <tr>
                          <td> {i + 1}</td>
                          <td> {location.district} </td>
                          <td> {location.taluk} </td>
                          <td> {location.updatedAt} </td>
                          {
                            // status badge
                          }

                          <td>
                            <Badge
                              className="badge badge-primary"
                              style={{ cursor: "pointer" }}
                              onClick={() =>
                                dispatch(
                                  districtdelete({
                                    id: location.id + "",
                                  })
                                )
                              }
                            >
                              Delete
                            </Badge>
                          </td>
                        </tr>
                      );
                    })
                  ) : (
                    <tr>
                      <td
                        colSpan={5}
                        style={{ textAlign: "center", fontWeight: "bold" }}
                      >
                        <Alert className="p-3" variant="danger">
                          {" "}
                          No Data Found{" "}
                        </Alert>
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AddDistrict;
